:root {
  --black: #0e0e0e;
  --white: #fbfbfb;
  --blue: #9dbfc9;
  --orange: #fe951b;
  --grey: #d0d0cf; }

/**
 * @param {string} $input-size - size in px
 * @param {string} $global-size - set to 16px
 */
.temp {
  display: inline-block;
  font-size: 2.25rem;
  margin-bottom: .5em; }
  .temp::after {
    border: solid 2px var(--grey);
    border-radius: 2px;
    content: '';
    display: block;
    margin-top: .5em; }
