:root {
  --black: #0e0e0e;
  --white: #fbfbfb;
  --blue: #9dbfc9;
  --orange: #fe951b;
  --grey: #d0d0cf; }

/**
 * @param {string} $input-size - size in px
 * @param {string} $global-size - set to 16px
 */
.form-search {
  margin-bottom: 1.875rem; }

.form-group {
  display: flex;
  flex-flow: row wrap; }
  .form-group input[type='text'] {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-right: none; }
  .form-group .submit {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px; }

input {
  font-size: 0.9375rem;
  padding: 8px 12px; }

input[type='text'] {
  border: solid 1px var(--grey);
  flex-grow: 2; }

.submit {
  background: var(--orange);
  border: solid 1px var(--orange);
  cursor: pointer;
  flex-grow: 1;
  max-width: 2.8125rem; }

label,
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden; }
